;\n}\n\nblockquote,\nq {\n  quotes: none;\n}\n\nblockquote:before,\nblockquote:after,\nq:before,\nq:after {\n  content: '';\n  content: none;\n}\n\ntable {\n  border-collapse: collapse;\n  border-spacing: 0;\n}\n\ninput {\n  appearance: none;\n  border-radius: 0;\n}\n","@import '../../../scss/global-resets';\n@import '../../../scss/fonts';\n@import '../../../scss/functions';\n@import '../../../scss/variables';\n@import '../../../scss/mixins';\n@import '../../../scss/global-styles';\n\n\n.swiper-slide {\n  align-items: center;\n  background: #fff;\n  color: $primary;\n  display: flex;\n  font-family: DINPro;\n  font-size: $size-3;\n  font-weight: $weight-medium;\n  height: 10rem;\n  justify-content: center;\n}\n\n","@font-face {\n  font-display: swap;\n  font-family: 'DINPro-Light';\n  font-style: normal;\n  font-weight: 300;\n  src:\n    font-path('DINPro-Light.woff2') format('woff2'),\n    font-path('DINPro-Light.woff') format('woff');\n}\n\n@font-face {\n  font-display: swap;\n  font-family: 'DINPro';\n  font-style: normal;\n  font-weight: 400;\n  src:\n    font-path('DINPro.woff2') format('woff2'),\n    font-path('DINPro.woff') format('woff');\n}\n\n@font-face {\n  font-display: swap;\n  font-family: 'DINPro-Medium';\n  font-style: normal;\n  font-weight: 500;\n  src:\n    font-path('DINPro-Medium.woff2') format('woff2'),\n    font-path('DINPro-Medium.woff') format('woff');\n}\n\n@font-face {\n  font-display: swap;\n  font-family: 'DINPro-Bold';\n  font-style: normal;\n  font-weight: 700;\n  src:\n    font-path('DINPro-Bold.woff2') format('woff2'),\n    font-path('DINPro-Bold.woff2') format('woff');\n}\n","/* COLORS */\n$black: #1c1c1c;\n$white: #fff;\n$light: #f8f8f8;\n$grey: #acacac;\n$primary: #ed1c24;\n$error: #c8362e;\n$success: #519735;\n$washed-red: #d59c9e;\n$night-blue: #323975;\n\n/* OVERLAY */\n$overlay-black: rgba(0, 0, 0, 0.2);\n\n$text-colors: (\n  'black' : $black,\n  'white': $white,\n  'grey': $grey,\n  'bs-red': $primary,\n  'light': $light,\n  'night-blue': $night-blue,\n);\n\n// Fonts Weight\n$weight-light: 300;\n$weight-regular: 400;\n$weight-medium: 500;\n$weight-bold: 700;\n\n// Font sizes\n$size-1: rem(46);\n$size-2: rem(40);\n$size-3: rem(36);\n$size-4: rem(26);\n$size-5: rem(20);\n$size-6: rem(16);\n$size-7: rem(14);\n\n$sizes: $size-1 $size-2 $size-3 $size-4 $size-5 $size-6 $size-7;\n\n// Shadow, find more smooth shadow: https://s.muz.li/MWNjYTY2Yjk4\n$shadow: 0 0.9px 2.6px rgba(0, 0, 0, 0.02),\n  0 3.1px 5.1px rgba(0, 0, 0, 0.03),\n  0 14px 14px rgba(0, 0, 0, 0.05)\n;\n\n// Easing\n$easing-smooth: cubic-bezier(0.5, 0, 0, 1);\n\n// Transition\n$transition: 0.2s ease;\n$transition-smooth: 0.5s $easing-smooth;\n\n// MEDIA QUERIES\n$breakpoints: (\n  // FOR IE\n  ie: '(-ms-high-contrast: active), (-ms-high-contrast: none)',\n  max-hugescreen:'(max-width:1599px)', min-hugescreen:'(min-width:1600px)',\n  max-fullhd: '(max-width:1407px)', min-fullhd: '(min-width:1408px)',\n  max-widescreen: '(max-width:1215px)', min-widescreen: '(min-width:1216px)',\n  max-desktop: '(max-width:1023px)', min-desktop: '(min-width:1024px)',\n  max-mobile: '(max-width:768px)', min-mobile: '(min-width:769px)',\n  max-small-mobile: '(max-width:540px)', min-small-mobile: '(max-width:540px)',\n  max-tiny: '(max-width:370px)', min-tiny: '(max-width:370px)',\n) !default;\n\n",".text {\n  @each $name, $color in $text-colors {\n    &-#{$name} {\n      color: $color !important;\n    }\n  }\n}\n\n.bg {\n  @each $name, $color in $text-colors {\n    &-#{$name} {\n      background-color: $color !important;\n    }\n  }\n}\n\nhtml {\n  background-color: $night-blue;\n}\n\na {\n  color: inherit;\n\n  &:hover {\n    color: $primary;\n  }\n}\n\nem {\n  color: $primary;\n  font-style: normal;\n}\n\nstrong {\n  color: inherit;\n  font-family: DINPro-Medium;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font: 15px/1.5rem DINPro, sans-serif;\n}\n\nh1 {\n  font-size: 48px;\n  line-height: 53px;\n  @include mq('min-desktop') {\n    font-size: 96px;\n    line-height: 106.8px;\n  }\n}\n\nh2 {\n  font-size: 30px;\n  line-height: 35px;\n  @include mq('min-desktop') {\n    font-size: 48px;\n    line-height: 53px;\n  }\n}\n\nh3 {\n  font-size: 28px;\n  line-height: 40px;\n  @include mq('min-desktop') {\n    font-size: 36px;\n    line-height: 52px;\n  }\n}\n\nh4 {\n  font-size: 20px;\n  line-height: 25px;\n  @include mq('min-desktop') {\n    font-size: 24px;\n    line-height: 36px;\n  }\n}\n\nh5 {\n  font-size: 16px;\n  line-height: 24px;\n}\n\nh6 {\n  font-size: 14px;\n  line-height: 24px;\n}\n\n\n.text-bold {\n  font-family: DINPro-Bold;\n}\n\n.text-upper {\n  text-transform: uppercase;\n}\n\n.text-center {\n  text-align: center;\n}\n\n.hide {\n  opacity: 0;\n  transition: opacity 1s;\n}\n\n.relative {\n  position: relative;\n}\n\n.button {\n  background-color: $white;\n  border: 0.1rem solid $primary;\n  border-radius: 0.2rem;\n  color: $primary;\n  cursor: pointer;\n  padding: 0.5rem 1rem;\n  text-decoration: none;\n\n  &:hover {\n    background: $primary;\n    color: $white;\n  }\n}\n\n.container-layout {\n  margin: 0 auto;\n  max-width: rem(1160);\n  width: 100%;\n\n  &.is-medium {\n    max-width: rem(960);\n  }\n\n  &.is-small {\n    max-width: rem(760);\n  }\n\n  &.is-smaller {\n    max-width: rem(540);\n  }\n\n  &.is-tiny {\n    max-width: rem(460);\n  }\n}\n","@use 'sass:math';\n\n@mixin mq($breakpoint-name) {\n  $breakpoint-name: unquote($breakpoint-name);\n  @if map-has-key($breakpoints, $breakpoint-name) {\n    $query: map-get($breakpoints, $breakpoint-name);\n    @media all and #{$query} {\n      @content;\n    }\n  } @else {\n    @warn '#{$mq-name} is not a value defined in the 'breakpoints' map.';\n  }\n}\n\n// BACKGROUND GRADIENT\n// https://robots.thoughtbot.com/controlling-color-with-sass-color-functions\n// https://glennmccomb.com/articles/creating-smooth-sequential-animations-with-sass/\n@mixin bg-gradient($directionTo: top, $colorStart: black, $colorEnd: white , $upTo: 100%) {\n  background: linear-gradient(to $directionTo, $colorStart 0%, $colorEnd $upTo);\n}\n\n@mixin pseudo($display: block, $pos: absolute, $content: '') {\n  content: $content;\n  display: $display;\n  position: $pos;\n}\n\n@mixin responsive-ratio($x, $y, $pseudo: true) {\n  $padding: unquote(math.div($y, $x) * 100 + '%');\n  @if $pseudo {\n    &:before {\n      @include pseudo($pos: relative);\n      padding-top: $padding;\n      width: 100%;\n    }\n  } @else {\n    > .ratio:first-child {\n      display: block;\n      padding-top: $padding;\n    }\n  }\n}\n"],"sourceRoot":""}]);
